import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import { Home, About, Posts, Category1, Category2, Category3, Footer } from '../pages';
import { Switch } from 'react-router-dom/cjs/react-router-dom.min';
// import Menu from '../components/Menu/Navigation';
import Sidebar from '../components/Menu/Sidebar';
import './Background.css';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { NavLink } from 'react-router-dom';

class App extends Component {
    render() {
        return (
            <div>
                {/* <div className="app-header">
                </div> */}
<header className="p-3 bg-dark text-white">
    <div className="container">
      <div className="d-flex flex-wrap align-items-center justify-content-center justify-content-lg-start">
        {/* <a href="/" className="d-flex align-items-center mb-2 mb-lg-0 text-white text-decoration-none">
          <svg className="bi me-2" width="40" height="32" role="img" aria-label="Bootstrap"><use xlink:href="#bootstrap"></use></svg>
        </a> */}

        <ul className="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0">
          <li><NavLink exact to="/" className="nav-link px-2 text-white">Home</NavLink></li>
          {/* <li><NavLink exact to="/" className="nav-link px-2 text-secondary">Home</NavLink></li> */}
          <li><a href="#" className="nav-link px-2 text-white">Features</a></li>
          <li><a href="#" className="nav-link px-2 text-white">Pricing</a></li>
          <li><a href="#" className="nav-link px-2 text-white">FAQs</a></li>
          <li><a href="#" className="nav-link px-2 text-white">About</a></li>
        </ul>

        {/* <form className="col-12 col-lg-auto mb-3 mb-lg-0 me-lg-3">
          <input type="search" className="form-control form-control-dark" placeholder="Search..." aria-label="Search">
        </form> */}

        <div className="text-end">
          <button type="button" className="btn btn-outline-light me-2">Login</button>
          <button type="button" className="btn btn-warning">Sign-up</button>
        </div>
      </div>
    </div>
  </header>


            <div className="app-container">
                <Sidebar />
                <div className="content-container overflow-auto">
                <Route exact path="/" component={Home}/>
                <Switch>
                  <Route path="/about/:name" component={About}/>
                  <Route path="/about" component={About}/>
                </Switch>
                <Switch>
                  <Route path="/category1/:name" component={Category1}/>
                  <Route path="/category1" component={Category1}/>
                </Switch>
                <Switch>
                  <Route path="/category2/:name" component={Category2}/>
                  <Route path="/category2" component={Category2}/>
                </Switch>
                <Route path="/category3" component={Category3}/>
                <Route path="/posts" component={Posts}/>
                </div>
            </div>
            <div className="app-bottom">
                <Footer />
            </div>
            </div>
        );
    }
}

export default App;